import { Injectable } from '@angular/core';
import { Workbook } from 'exceljs';
import * as fs from 'file-saver';

import { DatePipe } from '../../node_modules/@angular/common';
import { Tables} from './electricity-center/request-constructor/Tables';
import {Workload} from './models/workload';

@Injectable({
  providedIn: 'root'
})
export class ExcelService {

  constructor(private datePipe:DatePipe) { }
  generateExcel(title:string,header:any[],data:any[]){
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet(title);
    let titleRow = worksheet.addRow([title]);
    worksheet.addRow([]);
    let headerRow = worksheet.addRow(header);

    // Cell Style : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: 'FFFFFF00' },
        bgColor: { argb: 'FF0000FF' }
      };
      cell.border = { top: { style: 'thin' }, left: { style: 'thin' }, bottom: { style: 'thin' }, right: { style: 'thin' } }
    });
    worksheet.addRow([]);
    data.forEach(d => {
      let row = worksheet.addRow(d);
      let qty = row.getCell(5);

      });
    workbook.xlsx.writeBuffer().then((data) => {
      let blob = new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      fs.saveAs(blob, 'Workload.xlsx');
    });
  }
  genRX(){
    const title = 'Car Sell Report';
    const header = ["Year", "Month", "Make", "Model", "Quantity", "Pct"];
    const data = [
      [2007, 1, "Volkswagen ", "Volkswagen Passat", 1267, 10],
      [2007, 1, "Toyota ", "Toyota Rav4", 819, 6.5],
      [2007, 1, "Toyota ", "Toyota Avensis", 787, 6.2],
      [2007, 1, "Volkswagen ", "Volkswagen Golf", 720, 5.7],
      [2007, 1, "Toyota ", "Toyota Corolla", 691, 5.4],

    ];
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet('Car Data');
    let titleRow = worksheet.addRow([title]);
    worksheet.addRow([]);
    let headerRow = worksheet.addRow(header);

    // Cell Style : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: 'FFFFFF00' },
        bgColor: { argb: 'FF0000FF' }
      };
      cell.border = { top: { style: 'thin' }, left: { style: 'thin' }, bottom: { style: 'thin' }, right: { style: 'thin' } }
    });
    worksheet.addRow([]);
    data.forEach(d => {
        let row = worksheet.addRow(d);
        let qty = row.getCell(5);
        let color = 'FF99FF99';
        if (+qty.value < 500) {
          color = 'FF9999'
        }
        qty.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: color }
        }});
      workbook.xlsx.writeBuffer().then((data) => {
        let blob = new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        fs.saveAs(blob, 'CarData.xlsx');
      }
    );
  }
  generateDataSet(name:string,arr:any[],title:string):void{
    let data=[];
    confirm("download file");
    console.log(Tables);
    let table = Tables.find(el=>el.name==name);
    let  header:any[]=table.boxes.map(el=>el.disp);
    console.log(header);
    data=this.CollectData(table.boxes.map(el=>el.name),arr);

    console.log(data);
    this.generateExcel(title,header,data);
  }
  CollectData(names:string[],arr:any[]):any[] {
    console.log(names);
    console.log(arr);
    let fArr=[];
    for(let i=0;i<arr.length;i++){
      let data=[];
      console.log(arr[i]);
      names.forEach(el=>data.push(arr[i][el]));
      fArr.push(data);
      console.log(data);
    }
    return fArr;
  }


}
